class surveyData{

  constructor(args){
    this.data = [];
    let i, j;
    if(typeof args.type === "undefined"){
      for (i = 0; i < args.num; i++) {
        let object = {
          key: i,
          size: {
            width: 100,
            height: 100,
            padding: 10
          },
          name: "student",
          low: args.numLow > 0,
          groups: [
            0, 1, 2
          ],
          values:[
          ]
        }
        for (j = 0; j < 8; j++) {
          object.values.push({
            key: j,
            value: args.numLow > 0? (Math.random() * 40) / 100: (Math.random() * 60 + 40) / 100
          });
        }
        this.data.push(object);
        args.numLow--;
      }
    }
    else {
      let range = 20;
      let pos = 0;
      for (i = 0; i < args.num; i++) {
        let object = {
          key: i,
          size: {
            width: 100,
            height: 100,
            padding: 10
          },
          name: "student",
          groups: [
            0, 1, 2
          ],
          values:[
          ]
        }

        for (j = 0; j < 8; j++) {
          let value;
          if(args.type===2){
            value = Math.random();
          } else {
            value = (Math.random() * range + pos) / 100;
          }
          object.values.push({
            key: j,
            value: value
          });
        }
        this.data.push(object);
        pos += range;
      }
    }
    if(typeof args.noShuffle === "undefined")
      this.data = this.shuffle(this.data);
  }

  shuffle(array) {
    let currentIndex = array.length, temporaryValue, randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {

      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  }
}



export default surveyData;
