import React, { Component } from 'react';
import MURV, { Gene } from 'murv-component';
import firebase, { sendData } from './firebase';
import surveyData from './surveyData';
import { Redirect } from 'react-router';
import { addVisType } from './Storage';

class SurveyStart extends Component {
	constructor(props){
		super(props);
		this.state = {
			gene: null,
			navigate: false,
			responses: {},
			total: Math.round(Math.random() * 3) + 2
		};
		this.surveyData = new surveyData({num: 24, numLow: this.state.total});
		this.exampleData = new surveyData({num: 2, numLow: 1, noShuffle: true});
		this.config = props.config;
		this.handleClick = this.handleClick.bind(this);
		this.nextPage = this.nextPage.bind(this);
		this.renderExamples = this.renderExamples.bind(this);
		this.date = new Date();
		this.now = this.date.getTime();
	}

	componentDidMount(){

		let ref = firebase.database().ref('types');
		let type;
		let self = this;
    ref = ref.orderByValue();
    ref.limitToFirst(1).once('value', function(snapshot) {
        let smallest = 0;
        snapshot.forEach(function(ss){
            smallest = ss.val();
        });
        let smallRef = ref.equalTo(smallest);
        smallRef.once('value', function (newSS) {
            let i = 0;
            let rand = Math.floor(Math.random() * newSS.numChildren());
            newSS.forEach(function (snapshot) {
                if (i === rand) {
                    let randRef = snapshot.ref;
                    randRef = randRef.parent.ref;
                    let newObj = {};
                    newObj[snapshot.key] = snapshot.val() + 1;
                    randRef.update(newObj);
                    type = snapshot.key;
                }
                i++;
            });
        }).then(function () {


							let params = {
								shape: parseInt(type[0]),
								color: Gene.color.VALUE_DEPENDANT,
								color_key: Gene.color_key.OFF,
								path_points: Gene.path_points.EVEN,
								path_mode: parseInt(type[1]),
								path_rotation: Gene.path_rotation.NONE,
								path_grouping: Gene.path_grouping.NONE,
								object_rotation: Gene.object_rotation.NONE,
								object_size: Gene.object_size.FULL,
								filter: Gene.filter.OFF,
								debugging: Gene.debugging.OFF
							}
						 let gene = new Gene(params);
							self.setState({
								type: type,
								gene: gene
							});
        });
    });
	}

	handleClick(event){
		event.preventDefault();
		if(!event.target.parentElement.classList.contains('selected') && event.target.parentElement.classList.contains('svg')){
			let resps;
			let key = event.target.parentElement.getAttribute("id");
			if(key in this.state.responses){
				event.target.parentElement.classList.remove("selected");
				resps = this.state.responses;
				delete resps[key];
				console.log("here", resps);
			} else {
				resps = this.state.responses;
				resps[key] = event.target.parentElement.getAttribute("data-low");
				event.target.parentElement.classList.add("selected");
			}
			console.log(resps);
			this.setState({responses: resps});
		}
	}

	nextPage(e){
		e.preventDefault();
		let self = this;
		addVisType(self.state.type);
		sendData("responses1",this.state.type, {
			responses: this.state.responses,
			total: this.state.total,
			time: new Date().getTime()  - this.now
		}, function(){
			self.props.history.push("/survey-two", {
				type: self.state.type
			});
		});

	}

	renderMURVS(data, noClick){
		const MURVComponents = data.map((item, i) => {
			this.config.data.dataset.object = item;
			let obj = { data: { dataset: { object: item}} };
			let onClick = (noClick) ? function(){}: this.handleClick;
			return(
				<div className="svg" onClick={ onClick } data-low={ item.low } id={ "id" + i } key={ "key" + i }>
					<MURV config={ obj } gene={ this.state.gene } key={ i } />
				</div>
			)
		});
		return MURVComponents;
	}

	renderExamples(){

	}

  render() {
		if(this.state.navigate){
			return <Redirect to="/survey-two" push={true} />;
		}
		if(this.state.gene !== null){
			return (
				<div className="App">
					<p>The two images directly below show a low graded (left) and high graded (right) student respectively.
Please select the lowest graded students from the grid below and continue when done.</p>
					<div className="example">
						{ this.renderMURVS(this.exampleData.data, true) }
					</div>
					<div className="murvs">
	        	{ this.renderMURVS(this.surveyData.data) }
					</div>
					<button onClick={this.nextPage}>Continue</button>
	      </div>
			)
		} else {
			return null;
		}


  }
}

export default SurveyStart;
