import React, { Component } from 'react';
import MURV, { Gene } from 'murv-component';
import surveyData from './surveyData';
import { sendData } from './firebase';

class SurveyEnd extends Component{
  constructor(props){
    super(props);
    this.config = props.config;
    this.surveyData = new surveyData({num:3, type: 2});
    this.handleClick = this.handleClick.bind(this);
		this.clearSelection = this.clearSelection.bind(this);
    this.submit = this.submit.bind(this);
		this.count = 0;
		this.now = new Date().getTime();
    this.state = {
      responses: []
    }
  }

  handleClick(event){
		event.preventDefault();
		if(!event.target.parentElement.classList.contains('selected') && event.target.parentElement.classList.contains('svg') && this.count < 5){
			event.target.parentElement.setAttribute('data-selected', this.count + 1)
			event.target.parentElement.classList.add('selected');
			if(this.state.responses === null){
				this.setState({
					responses: [event.target.parentElement.getAttribute('data-type')]
				})
			} else {
				let resp = this.state.responses;
				resp = resp.concat(event.target.parentElement.getAttribute('data-type'));
				this.setState({
					responses: resp
				})
			}
      this.count++;
		}
	}

  clearSelection(event){
		event.preventDefault();
		let divs = document.querySelectorAll('.selected'), i;
		for (i = 0; i < divs.length; i++) {
			divs[i].classList.remove('selected');
			divs[i].setAttribute('data-selected', '0');
			this.count = 0;
			this.setState({
				responses: []
			})
		}
	}

  submit(event){
    event.preventDefault();
    let self = this;
    sendData("responses3", new Date().getTime(), {
			responses: this.state.responses,
			time: new Date().getTime()  - this.now,
      dayToDay: document.querySelector('#howTheyWouldUse').value,
      liked: document.querySelector('#aspectLiked').value,
      disliked: document.querySelector('#aspectDisliked').value
		}, function(){
			self.props.history.push("/survey-one");
		});
  }

  renderMURVS(types){
    console.log(types);
		const MURVComponents = types.map((type, i) => {
      let item = this.surveyData.data[1];
      console.log(item)
			this.config.data.dataset.object = item;
			let obj = { data: { dataset: { object: item}} };
			return(
				<div data-type={ "" + type } key={ i } className="svg" onClick={ this.handleClick }>
					<MURV config={ obj } gene={ this.getGene(type) } key={ i } />
				</div>
			)
		});
		return MURVComponents;
	}

  getGene(type){
    let params = {
			shape: parseInt(type[0]),
			color: Gene.color.VALUE_DEPENDANT,
			color_key: Gene.color_key.OFF,
			path_points: Gene.path_points.EVEN,
			path_mode: parseInt(type[1]),
			path_rotation: Gene.path_rotation.NONE,
			path_grouping: Gene.path_grouping.NONE,
			object_rotation: Gene.object_rotation.NONE,
			object_size: Gene.object_size.FULL,
			filter: Gene.filter.OFF,
			debugging: Gene.debugging.OFF
		}
	 return new Gene(params);
  }

  render(){
    let types = JSON.parse(sessionStorage.getItem('types'));
    return(
      <div className="app">
        <p>Choose your top 5 visualisations (you have seen versions of all of these designs already).</p>
        { this.renderMURVS(types) }

        <button onClick={this.clearSelection}>Clear Selection</button>

        <p>Please answer the following questions:</p>
        <p>Describe how you would like to use these visualisations in day to day life</p>
        <textarea id="howTheyWouldUse"></textarea>
        <p>Describe some aspects that you liked about the glyph designs.</p>
        <textarea id="aspectLiked"></textarea>
        <p>Describe some aspects that you disliked about the glyph designs.</p>
        <textarea id="aspectDisliked"></textarea>

        <button onClick={ this.submit }>Finish</button>
      </div>
    )
  }
}

export default SurveyEnd;
