import React, { Component } from 'react';
import MURV, { Gene } from 'murv-component';
import GeneGenerator from './components/GeneGenerator';

class Page extends Component {
	constructor(props){
		super(props);
		this.geneGenerator = new GeneGenerator();
		this.config = props.config;
		this.gene = new Gene(
			Gene.shape.CIRCLE,
			Gene.color.VALUE_DEPENDANT,
			Gene.color_key.OFF,
			Gene.path_points.EVEN,
			Gene.path_mode.CUBE_SPIRAL,
			Gene.path_rotation.NONE,
			Gene.path_grouping.NONE,
			Gene.object_rotation.NONE,
			Gene.object_size.FULL,
			Gene.filter.OFF,
			Gene.debugging.OFF
		);
	}

	renderMURVS(){
		let string = "[";
		const MURVComponents = this.geneGenerator.genes.map((item, i) => {
			let shape = item.shape.toString(16);
			let path_mode = item.path_mode.toString(16);
			string += "\"" + shape + "" + path_mode + "\",";
			return(
				<MURV config={ this.config } gene={ item } key={ i } />
			)
		});
		string += "]";
		console.log(string);
		return MURVComponents;
	}

  render() {
    return (
      <div className="App">
        { this.renderMURVS() }
      </div>
    );
  }
}

export default Page;
