import React, { Component } from 'react';
import MURV, { Gene } from 'murv-component';
import surveyData from './surveyData';
import { sendData } from './firebase';

class SurveyTwo extends Component {
	constructor(props){
		super(props);

		this.surveyData = new surveyData({num:5, type: 1});
		this.config = props.config;
		this.state = {
			gene: null,
			responses: null,
			type: typeof this.props.location.state != "undefined" ? this.props.location.state.type : "11"
		};
		this.handleClick = this.handleClick.bind(this);
		this.nextPage = this.nextPage.bind(this);
		this.clearSelection = this.clearSelection.bind(this);
		this.count = 0;
		this.now = new Date().getTime();

	}

	componentDidMount(){
		let params = {
			shape: parseInt(this.state.type[0]),
			color: Gene.color.VALUE_DEPENDANT,
			color_key: Gene.color_key.OFF,
			path_points: Gene.path_points.EVEN,
			path_mode: parseInt(this.state.type[1]),
			path_rotation: Gene.path_rotation.NONE,
			path_grouping: Gene.path_grouping.NONE,
			object_rotation: Gene.object_rotation.NONE,
			object_size: Gene.object_size.FULL,
			filter: Gene.filter.OFF,
			debugging: Gene.debugging.OFF
		}
	 let gene = new Gene(params);
		this.setState({
			gene: gene
		});
	}

	handleClick(event){
		event.preventDefault();
		if(!event.target.parentElement.classList.contains('selected') && event.target.parentElement.classList.contains('svg')){
			event.target.parentElement.setAttribute('data-selected', this.count + 1)
			event.target.parentElement.classList.add('selected');
			if(this.state.responses === null){
				this.setState({
					responses: [parseInt(event.target.parentElement.getAttribute('data-order')) === this.count++]
				})
			} else {
				let resp = this.state.responses;
				resp = resp.concat(parseInt(event.target.parentElement.getAttribute('data-order')) === this.count++);
				this.setState({
					responses: resp
				})
			}
		}
	}

	nextPage(event){
		event.preventDefault();
		let self = this;
		sendData("responses2",this.state.type, {
			responses: this.state.responses,
			time: new Date().getTime()  - this.now
		}, function(){
			let qNum = parseInt(sessionStorage.getItem("question"));
			if(qNum === 20){
				self.props.history.push("/survey-three");
			} else {
				sessionStorage.setItem(
					"question",
					qNum + 1
				);
				window.dispatchEvent( new Event('storage') );
				self.props.history.push("/survey-one");
			}
		});
	}

	clearSelection(event){
		event.preventDefault();
		let divs = document.querySelectorAll('.selected'), i;
		for (i = 0; i < divs.length; i++) {
			divs[i].classList.remove('selected');
			divs[i].setAttribute('data-selected', '0');
			this.count = 0;
			this.setState({
				responses: []
			})
		}
	}

	renderMURVS(){
		const MURVComponents = this.surveyData.data.map((item, i) => {
			this.config.data.dataset.object = item;
			let obj = { data: { dataset: { object: item}} };
			return(
				<div data-order={ "" + item.key } key={ i } className="svg" onClick={ this.handleClick }>
					<MURV config={ obj } gene={ this.state.gene } key={ i } />
				</div>
			)
		});
		return MURVComponents;
	}

  render() {
		if(this.state.gene !== null){
			return (
				<div className="App">
					<p>Rank the following students in order, from lowest to highest.</p>
	        { this.renderMURVS() }
					<div className="clear"></div>
					<button onClick={this.nextPage}>Continue</button>
					<button onClick={this.clearSelection}>Clear Selection</button>

	      </div>
			)
		} else {
			return null;
		}

  }
}

export default SurveyTwo;
