import React, { Component } from 'react';
import MURV, { Gene } from 'murv-component';

class Glance extends Component {
	constructor(props){
		super(props);
		let params = {
			shape: Gene.shape.BAR,
			color: Gene.color.FROM_DATA,
			color_key: Gene.color_key.OFF,
			path_points: Gene.path_points.EVEN,
			path_mode: Gene.path_mode.INLINE,
			path_rotation: Gene.path_rotation.NONE,
			path_grouping: Gene.path_grouping.NONE,
			object_rotation: Gene.object_rotation.NONE,
			object_size: Gene.object_size.FULL,
			filter: Gene.filter.OFF,
			debugging: Gene.debugging.OFF
		}
		this.gene1 = new Gene(params);

		params.shape = Gene.shape.DONUT;
		params.path_mode = Gene.path_mode.RING;
		params.path_points = Gene.path_points.VALUE_DEPENDANT;
		this.gene2 = new Gene(params);

		params.shape = Gene.shape.RADIAL_BAR;
		params.path_mode = Gene.path_mode.INLINE_HALF;
		params.path_points = Gene.path_points.EVEN;
		this.gene3 = new Gene(params);
	}

  render() {
    return (
			<div className="glanceable">
	      <MURV config={ this.props.config } gene={ this.gene1 }/>
	      <MURV config={ this.props.config } gene={ this.gene2 }/>
	      <MURV config={ this.props.config } gene={ this.gene3 }/>
			</div>
    );
  }
}

export default Glance;
