import React, { Component } from 'react';
import {
  BrowserRouter,
  Route,
  NavLink} from 'react-router-dom';
import SurveyStart from './survey-start';
import SurveyOne from './survey-1';
import SurveyTwo from './survey-2';
import SurveyEnd from './survey-3';
import Page from './page';
import Glance from './Glance'
import Config from './config'

class App extends Component {
  constructor(props){
    super(props);
    this.props = props;
    sessionStorage.setItem("question", "1");
    // sessionStorage.removeItem("types");
    this.localStorageUpdated = this.localStorageUpdated.bind(this);
    this.state = {
      question: 1
    }
  }
  componentDidMount(){
    window.addEventListener('storage', this.localStorageUpdated)
  }
  localStorageUpdated(){
    console.log("now");
    this.setState({question: parseInt(sessionStorage.getItem("question"))});
  }
  render(){
    return(
      <BrowserRouter>
        <div>
          <h1><u>M</u>icro <u>U</u>nit-based <u>R</u>eact <u>V</u>isualisations Survey</h1>
        </div>
        <div className="content">
          <p><strong>Question { this.state.question  } of 20</strong></p>
          <Route exact path="./" render={(props) => <SurveyStart {...props} />} />
          <Route path="/survey-one" render={props => <SurveyOne config={Config} {...props}/>} />
          <Route path="/survey-two" render={props => <SurveyTwo config={Config} {...props}/>} />
          <Route path="/survey-three" render={props => <SurveyEnd config={Config} {...props}/>} />
          <Route path="/glanceable" component={() => <Glance config={Config}/>} />
        </div>
      </BrowserRouter>
    )
  }
}

export default App;
